<template>
  <div id="show" class="title">
    <div style="float:left;margin-left:3rem;margin-top: 1.8rem">
      <span @click="goIndex" class="fontLinght_long" style="cursor: pointer">FREEBLOCK.LUXE</span>
    </div>
    <div style="float:right;margin-right:3rem;margin-top: 1.8rem">
      <span @click="NotLogin" style="cursor:default;">{{this.$store.getters.getNetworkShort}}</span>
      <router-link :to="{name:'seashells'}" target="_blank" style="text-decoration: none;color: white; margin-left: 1.2rem; cursor:pointer">seashells</router-link>
      <span @click="goWriteBook" target="_blank" style="margin-left: 1.2rem; cursor:pointer">白皮书</span>
      <router-link :to="{name:'apply'}" target="_blank" style="text-decoration:none; color: white;margin-left: 1.2rem;cursor: pointer">申请上币</router-link>
    </div>
  </div>
</template>
<script>
import router from '@/router/index.js'
export default {
  methods: {
    goIndex () {
      router.push({path: '/'})
    },
    NotLogin () {
      if (this.$store.getters.getNetworkShort.indexOf('Not') > -1) {
        alert('you have not login Metamask')
      } else {
        alert('Metamask status\nMay the force be with you')
      }
    },
    goApply () {
      // router.push({name: 'apply'})
      // 在新窗口中打开 build后无效
      let routeUrl = this.$router.resolve({
        path: '/apply'
      })
      window.open(routeUrl.href, '_blank')
      console.log('apply', routeUrl.href)
    },
    goSeashells () {
      router.push({name: 'seashells'})
      // // 在新窗口中打开 build后无效
      // let routeUrl = this.$router.resolve({
      //   path: '/seashells'
      // })
      // window.open(routeUrl.href, '_blank')
    },
    goWriteBook () {
      window.open('https://github.com/freeblockLuxe/Freeblock.luxe/blob/master/ExchangeWhitebook.md')
    }
  }
}
</script>
<style scoped>
  .title {
    font-family: h;
    color: white;
    width: 100%;
    height: 5rem;
    margin: 0;
    padding: 0;
    text-align: center;
  }

  .fontLinght_long {
    font-size: large;
    text-shadow:
    0 0 1.5rem white,
    0 0 2.5rem white;
  }
</style>



