<template>
  <div id="show" class="title">
    <div style="float:left;margin-left:3rem;margin-top: 1.8rem">
      <span @click="goIndex" class="fontLinght_long" style="cursor: pointer">FREEBLOCK.LUXE</span>
    </div>
    <div style="float:right;margin-right:3rem;margin-top: 1.8rem">
      <span @click="NotLogin" style="cursor:default; color: white">{{this.$store.getters.getNetworkShort}}</span>
      <router-link :to="{name:'seashells'}" target="_blank" style="text-decoration: none; color: black; margin-left: 1.2rem;cursor: pointer">seashells</router-link>
      <span @click="goWriteBook" style="margin-left: 1.2rem; cursor:pointer">白皮书</span>
      <router-link :to="{name:'apply'}" target="_blank" style="text-decoration: none; color: black; margin-left: 1.2rem;cursor: pointer">申请上币</router-link>
    </div>
  </div>
</template>
<script>
import router from '@/router/index.js'
export default {
  methods: {
    goIndex () {
      router.push({path: '/'})
    },
    NotLogin () {
      if (this.$store.getters.getNetworkShort.indexOf('Not') > -1) {
        alert('you have not login Metamask')
      } else {
        alert('May the force be with you')
      }
    },
    goApply () {
      router.push({name: 'apply'})
    },
    goSeashells () {
      router.push({name: 'seashells'})
    },
    goSourceCode () {
      // router.push({name: 'SourceCode'})
      // window.location.href = 'https://github.com/freeblockworld/freeblock.wrold/blob/master/seashells'
      window.open('https://etherscan.io/address/0x5cf4C6eE0d1F6F9337AB89A79F1885C748b8beE1#code')
    },
    goWriteBook () {
      window.open('https://github.com/freeblockLuxe/Freeblock.luxe/blob/master/SeashellsWhitebook.md')
    }
  }
}
</script>
<style scoped>
  .title {
    font-family: h;
    width: 100%;
    height: 6.2rem;
    margin: 0;
    padding: 0;
    text-align: center;
    background-color:#FF6B4A;
    color: black;
  }
  /*.logoTitle {*/
    /*background-image: url('/static/pic/logo72.jpg');*/
    /*background-repeat: no-repeat;*/
    /*background-size: cover;*/
    /*width: 52px;*/
    /*height: 52px;*/
    /*margin: 0 auto;*/
    /*border: 1px solid white;*/
    /*border-radius: 3px;*/
  /*}*/
  .fontLinght_long {
    font-size: large;
    text-shadow:
    0 0 1.5rem #000,
    0 0 2.5rem #FF6B4A
  }
</style>



