    background: linear-gradient(to right, #9980D6, #4BBF71);
  }
  .input-box {
    width:100%;
    margin-top: 0.5rem;
    margin-bottom: 0.5rem;
    padding: 0.18rem;
  }
  .input-text {
    margin: 0 ;
    padding: 0 ;
    height: 2.8rem;
    width: 80%;
    font-size: 1rem;
    border: 1px solid #D8D8D8;
    border-image: none;
    text-indent: 0.2rem;
    float: left;
  }
  .input-submit {
    margin: 0 ;
    padding: 0 ;
    font-size: 1rem;
    background: transparent;
    border:1px solid white;
    height: 2.8rem;
    width: 20%;
    color: white;
    -moz-appearance: none;
    text-align: left;
    text-indent: 0.2rem;
    float: left;
    cursor: pointer;
  }

</style>



