            <div class="block_text" style="font-size: xx-large; margin-top: 1.8rem">All-Time Averages</div>
            <div class="block_text">Of The first bonded token</div>
            <div class="block_text"><span>total tokens: </span><span>{{this.$store.getters.getTotalToken}} ss</span>
            </div>
            <div class="block_text">
              <span>total dividends: </span><span>{{this.$store.getters.getTotalDividends}} eth</span></div>
            <div class="block_text"><span>buy Price: </span><span>{{this.$store.getters.getBuyPrice}} eth</span></div>
            <div class="block_text"><span>sell Price: </span><span>{{this.$store.getters.getSellPrice}} eth</span></div>
          </div>
