        <div class="guideline">
          <div style="color: #FF6B4A"><a style="text-decoration-line: none" target="_blank" href="https://metamask.io/">Metamask</a></div>
          <div>install Metamask</div>
        </div>
        <div class="guideline" style="margin-top:0.5rem;height: 0.1rem;width:1rem; border:1px solid #FF6B4A;"/>
        <div class="guideline">
          <div style="color: #FF6B4A">Login Metamask</div>
          <div>deposit ETH in you account</div>
        </div>
        <div class="guideline" style="margin-top:0.5rem;height: 0.1rem;width:1rem; border:1px solid #FF6B4A;"/>
        <div class="guideline">
          <div style="color: #FF6B4A">Buy</div>
          <div>buy & holding seashells</div>
        </div>
        <div class="guideline" style="margin-top:0.5rem;height: 0.1rem;width:1rem; border:1px solid #FF6B4A;"/>
        <div class="guideline">
          <div style="color: #FF6B4A">Sell</div>
          <div>sell seashells receipt ETH</div>
        </div>
        </div>
      </div>

      <!--officia Referral link-->
      <div style="clear:both;"></div>
      <div style="padding-top: 3rem; background-color:#F7F7F7;">
