          <div>Official Referral link: The revenue will be used for developers Reward funds</div>
          <div @click="goOfficialReferral" style="text-decoration-line:underline;cursor: pointer">{{this.$store.getters.getOfficialReferral}}</div>
        </div>
      </div>

      <!--part3-->
      <div style="clear:both;"></div>
      <div style="background-color: #F7F7F7; width: 100% ;">
        <div class="floatCenter">
          <!--buy-->
          <div class="part3_4_div_block">
            <div style="height: 16.25rem">
              <div class="block_text" style="font-size: xx-large; margin-top: 1.8rem">Create Tokens!</div>
              <div class="block_text">Purchase with ETH from your wallet, or reinvest from your Vault to compound your
                dividend income
              </div>
              <div class="input-box" style="margin-top: 1rem">
                <input class="input-text" type="text" placeholder=" >0.02 eth" v-model="buyInput"/>
                <input id="buy_calculate" class="input-submit" type="submit" @click="calculateTokensReceived" value="= ss" />
              </div>
              <div class="block_text" style="margin-top: 2.5rem">There is a 10% Tax, that's how the token works after all.</div>
