              <div class="block_text">Sell your seashells instantly at any time for ETH</div>
              <div class="input-box" style="margin-top: 1rem">
                <input class="input-text" type="text" placeholder="> 1 ss" v-model="sellInput"/>
                <input id="sell_calculate" class="input-submit" type="submit" @click="calculateEthereumReceived" value="= eth" />
              </div>
              <div class="block_text" style="margin-top: 3rem">Note: ETH will be deposited in your vault. You need to manually withdraw it to
                your wallet.</div>
              <div class="block_text" style="font-size: medium">Need to pay gas to the Ethereum</div>
            </div>
            <div style="text-align: center; margin-top: 0.1rem">
              <button class="block_button" @click="Sell">sell</button>
            </div>
          </div>
        </div>
      </div>

      <!--part4-->
      <div style="clear:both;"></div>
