          <!--Referral-->
          <div class="part3_4_div_block">
            <div class="block_text" style="font-size: xx-large; margin-top: 1.8rem">Master node:</div>
            <div class="block_text" style="font-size: medium">{{this.$store.getters.getCustomerAddress}}</div>
            <div class="block_text">Owning 100 seashells unlocks the Master node / Referral System</div>
            <div class="block_text">Your referral link and you can share with your friends:</div>
            <div class="block_text" style="height: 6rem; margin-top: 1.2rem; border:0.05rem dashed #FF6B4A;border-radius: 0.1rem">
              <div class="block_text" style="word-break: break-word; word-wrap: normal; font-size: medium">{{this.$store.getters.getMyReferredLink}}</div>
            </div>
            <div class="block_text" style="font-size: medium">30% of dividends a player uses to buy ss  immediately is kicked back to you as a referral reward</div>

          </div>
          <!--vault-->
          <div class="part3_4_div_block" style="margin-left: 0.56rem">
