                style="margin-left: 0.56rem">{{this.$store.getters.getNetworkShort}}</span></div>
              <div class="block_text"><span>Your seashells: </span><span style="margin-left: 3px">{{this.$store.getters.getMyTokens}} ss</span>
              </div>
              <div class="block_text"></div>
              <div class="block_text"><span>Your dividends: </span><span style="margin-left: 3px">{{this.$store.getters.getMyDividends}} eth</span>
