</template>

<script>
import seashellsSC from '../js/seashells'
import myTitle from '../components/Title'
import myFoot from '../components/Foot'

export default {
  name: 'seashells',
  components: {myTitle, myFoot},
  data: function () {
    return {
      titleShow: '',
      title: '',
      buyHint: '',
      buyInput: '',
      sellHint: '',
      sellInput: '',
      inputParameter: '',
      myReferredLink: ''
    }
  },
  watch: {
    buyInput: function () {
      this.buyInput = (this.buyInput.match(/^\d*(\.?\d{0,4})/g)[0]) || null
    },
    sellInput: function () {
      if (this.sellInput.substring(0, 1) !== '0') { // 不能以0开关
        this.sellInput = this.sellInput.replace(/[^\d]/g, '') // 只接整形数字
      } else {
        this.sellInput = ''
      }
    }
  },
  beforeCreate () {
    document.querySelector('body').setAttribute('style', 'margin: 0; padding:0; background-color:#FF6B4A;')
  },
  // 钩子函数 组件加载完成 数据初始化
  mounted () {
    // 加载推荐者
    if (typeof this.$route.query.ss !== 'undefined' && seashellsSC.web3js.isAddress(this.$route.query.ss)) {
      this.$store.commit('setReferredBy', this.$route.query.ss)
    }

    console.log(this.$options.name, ' log in by:', this.$route.query.ss)

    seashellsSC.initContract()
  },
  // 方法集合
  methods: {

    Buy () {
      var buyQty = this.buyInput
      seashellsSC.buy(buyQty)
    },
    Sell () {
      var sellQty = this.sellInput
      seashellsSC.sell(sellQty)
    },

    goOfficialReferral () {
      window.open(this.$store.getters.getOfficialReferral)
    },
    goTradingCenter () {
      // router.push({name: 'token', query: {token: this.$store.getters.getSysTokenSymbol}})
      let routeUrl = this.$router.resolve({
        path: '/token',
        query: {token: this.$store.getters.getSysTokenSymbol}
      })
      window.open(routeUrl.href, '_blank')
    },
    /**
     * 取走请求者的所有收益。
     */
    withdraw () {
      seashellsSC.withdraw()
    },
    /**
     * 取回请求者拥有的股息。
     * 如果`_includeReferralBonus` 的值为1，那么推荐奖金将被计算在内。
     * 其原因是，在网页的前端，我们希望得到全局汇总。
     * 但在内部计算中，我们希望分开计算。
     */
    // myDividends () {
    //   var res = seashellsSC.seashells.myDividends.call(1)
    //   console.log('myDividends: ' + seashellsSC.web3.fromWei(parseInt(res)))
    // },

    // 将所有的红利再投资为代币。包括股息和分红
    reinvest () {
      seashellsSC.reinvest()
    },

    // 计算输入eth的可买入数量
    calculateTokensReceived () {
      var inputValue = this.buyInput
      seashellsSC.calculateTokensReceived(inputValue)
    },
    // 动态获取卖出订单价格
    calculateEthereumReceived () {
      var inputValue = this.sellInput
      seashellsSC.calculateEthereumReceived(inputValue)
    }

  },

  // 钩子函数 组件结束前执行
  beforeDestroy () {
    console.log(this.$options.name, ' log out')
    document.querySelector('body').removeAttribute('style')
  }
}
</script>

<style scoped>
  .background {
    text-align: center;
    margin: 0;
    padding: 0;
    border: 0;
    width: 100%;
    height: 100%;
    background-color:#FF6B4A;
  }
  .guideline{
    float: left;
    margin-left: 0.5rem;
  }
  .part2_div_block{
    text-align:left;
    width: 20rem;
    height: 20rem;
    border:1px solid #FF6B4A;
    display: block;
    float: left;
    background-color:white;
  }
  .part3_4_div_block {
    text-align:left;
    width: 30rem;
    height: 22.5rem;
    display: block;
    float: left;
    margin-top: 2rem;
    background-color:white;
    border-radius: 0.3rem;
    box-shadow:
    0 0 1rem #FF6B4A;
  }
  .block_text {
    font-size: large;
    margin-left: 0.4rem;
    margin-right: 0.4rem;
    margin-top: 0.56rem;
  }
  .block_button {
    width: 12.5rem;
    height: 2.5rem;
    font-size: large;
    background-color: #FF6B4A;
    color: rgba(250,250,250,1);
    margin:0.56rem auto 0;
    cursor: pointer;
  }
  .input-box {
    width:100%;
    margin-top: 0.5rem;
    margin-bottom: 0.5rem;
    padding: 0.18rem;
  }
  .input-text {
    margin: 0 ;
    padding: 0 ;
    height: 2rem;
    width: 50%;
    font-size: 1rem;
    border: 1px solid #D8D8D8;
    border-image: none;
    text-indent: 0.2rem;
    float: left;
  }
  .input-submit {
    margin: 0 ;
    padding: 0 ;
    font-size: 1rem;
    background-image: linear-gradient(to bottom, #FBFBFB, #F3F3F3);
    border: 1px solid #D8D8D8;
    cursor: pointer;
    height: 2rem;
    width: 50%;
    -moz-appearance: none;
    text-align: left;
    text-indent: 0.2rem;
    float: left;
    }
  .floatCenter{
    margin: 0 auto;
    display: inline-block;
  }
  .buttonComm {
    color: white;
    width: 12rem;
    height: 3rem;
    font-family: Helvetica;
    background: transparent;
    border:1px solid white;
    border-radius: 0.3rem;
    box-shadow: 0 0 1rem white;
    cursor: pointer;
  }
  .curved_box {
    display: inline-block;
    *display: inline;
    width: 12.5rem;
    height: 15.5rem;
    margin: 1.25rem;
    background-color: #fff;
    border: 0.06rem solid #eee;
    -webkit-box-shadow: 0 0.6rem 2.5rem rgba(0, 0, 0, 0.27), 0 0 5.6rem rgba(0, 0, 0, 0.06) inset;
    -moz-box-shadow: 0 0.6rem 2.5rem rgba(0, 0, 0, 0.27), 0 0 5.6rem rgba(0, 0, 0, 0.06) inset;
    box-shadow: 0 0.6rem 2.5rem rgba(0, 0, 0, 0.27), 0 0 5.6rem rgba(0, 0, 0, 0.06) inset;
    position: relative;
    *zoom: 1;
  }

</style>



