<template>
      <div @click="onClick" class="coinSpan" >
        <span style="position: absolute; width: 7rem; margin-left: 2rem; line-height: 2.9rem;text-align: left">{{tokenSymbol}}</span>
        <span style="margin-left: 8rem; width: 7rem; line-height: 2.9rem; text-align: left;">{{tokenPrice}}</span>
        </div>
</template>

<script>
import router from '@/router/index.js'
export default {
  name: 'tokenHead',
  props: {
    tokenSymbol: '',
    tokenPrice: ''
  },
  watch: {
    // 同一页面不同参数时刷新页面
    '$route' (to, from) {
      this.$router.go(0)
    }
  },
  data: function () {
    return {
    }
  },
  // 钩子函数 组件加载时执行
  created () {
  },
  // 方法集合
  methods: {
    onClick () {
      router.push({path: '/token', query: {token: this.tokenSymbol}})
    }
  }
}
</script>

<style scoped>
  .coinSpan {
    text-align: center;
    width: 100%;
    height: 3rem;
    margin: 0.5rem;
    cursor: pointer;
  }
</style>



