// 检查浏览器
function checkExpress () {
  let ok = 0
  if (navigator.userAgent.indexOf('Firefox') >= 0) {
    ok = 1
  } else if (navigator.userAgent.indexOf('Opera') >= 0) {
    ok = 1
  } else if (navigator.userAgent.indexOf('Chrome') >= 0) {
    ok = 1
  } else if (navigator.userAgent.indexOf('Brave') >= 0) {
    ok = 1
  }
  console.log('express:' + ok)
  if (ok === 0) {
    // if (confirm('以太坊metamask不支持当前浏览器
    // 您可以使用360,Chrome, Firefox, Opera, Brave
    // 不能访问google的用户  可使用360或Firefox浏览器\n详情可查看https://metamask.io')) {
    //   window.open('https://metamask.io')
    // }

    alert('以太坊metamask插件不支持当前浏览器 \n您可以使用360, Chrome, Firefox等支持metamask插件的浏览器 \n不能访问google的用户  可使用360或Firefox浏览器')
  }
  return ok
}

function initContract (coinSymbol) {
  // 检查当前token是否存在
  let currToken = coinAbi.findCoinBySymbol(coinSymbol)
  if (typeof currToken.abi === 'undefined') {
    alert('not find token' + coinSymbol)
    return
  }
  Store.commit('setTokenHostNetwork', currToken.host)
  let tokenHost
  if (typeof window.web3 !== 'undefined') {
    window.web3.version.getNetwork((er, netId) => {
      if (typeof netId !== 'undefined') {
        Store.commit('setNetwork', netId)
        if (netId === '1') { // 是否为主网
          Store.commit('setNetworkShort', 'Main Net')
          console.log('tokenBase metamask network is Main:', netId)
        } else {
          Store.commit('setNetworkShort', 'Test Net')
          console.log('tokenBase metamask network is Test:', netId)
        }

        // 是否预设网络（开发阶段为测试网 发布阶段为主网）
        // if (netId === Store.getters.getPresetNetwork) {
        //   console.log('network is right:' + netId)
        //   eth_ = new Eth(window.web3.currentProvider)
        // } else {
        //   console.log('network is wrong: ' + netId)
        //   eth_ = new Eth(new Web3.providers.HttpProvider(Store.getters.getMustNetworkHttp))
        // }
        if (netId === '1') {
          if (currToken.host === '1') {
            tokenHost = new Eth(window.web3.currentProvider)
          } else {
            tokenHost = new Eth(new Web3.providers.HttpProvider(Store.getters.getRinkebyInfura))
          }
        } else {
          if (currToken.host === '1') {
            tokenHost = new Eth(new Web3.providers.HttpProvider(Store.getters.getMainInfura))
          } else {
            tokenHost = new Eth(window.web3.currentProvider)
          }
        }
      } else {
        console.log('network is undefined')
        if (currToken.host === '1') {
          tokenHost = new Eth(new Web3.providers.HttpProvider(Store.getters.getMainInfura))
        } else {
          console.log('else rinkeby main')
          tokenHost = new Eth(new Web3.providers.HttpProvider(Store.getters.getRinkebyInfura))
        }
      }

      let ethContract = new EthContract(tokenHost)

      tokenContract = ethContract(currToken.abi).at(currToken.address)

      Store.commit('setContractAddress', currToken.address)
      console.log('contractAddress' + Store.getters.getContractAddress)

      getETHGasPrice()
      getStaticInfo()
      getDynamicInfo()
    })
  } else { // 当没有运行metamask时 通过加载infura获取只读数据 让程序可以正常显示
    console.log('not metamask & connect: ' + Store.getters.getTokenHostNetwork)
    // let eth_ = new Eth(new Web3.providers.HttpProvider(Store.getters.getMustNetworkHttp))
    //
    // let ethContract = new EthContract(eth_)
    //
    // let findCoin = coinAbi.findCoinBySymbol(coinSymbol)
    //
    // if (typeof findCoin.abi === 'undefined') {
    //   alert('not find coin' + coinSymbol)
    //   return
    // }

    if (currToken.host === '1') {
      tokenHost = new Eth(new Web3.providers.HttpProvider(Store.getters.getMainInfura))
    } else {
      tokenHost = new Eth(new Web3.providers.HttpProvider(Store.getters.getRinkebyInfura))
    }

    let ethContract = new EthContract(tokenHost)

    tokenContract = ethContract(currToken.abi).at(currToken.address)

    Store.commit('setContractAddress', currToken.address)
    console.log('contractAddress' + Store.getters.getContractAddress)

    getStaticInfo()
    getDynamicInfo()
  }
  return tokenContract
}

// 获取以太坊公有数据 需在有插件[window.web3]的状态下
function getETHGasPrice () {
  // 获取当前的gasPrice 如果大于预设值 则取当前动态值
  window.web3.eth.getGasPrice((er, result) => {
    if (result !== null && result > 0) {
      Store.commit('setGasPrice', result)
    }
  })
}

function getTradingFromWebApi (token) {
  console.log('TradingFromWebApi', token)
  // 从数据库加载交易记录至总成交列表
  vm.$http.get('/api/EXTrade?token=' + token, {emulateJSON: true})
    .then((response) => {
      if (response.data.length > 0 && response.data.length < 100) {
        for (let i = 0; i < response.data.length; i++) {
          // 检查sysID 是否重复
          if (!Global.getArrayListSysID(Store.state.tokens.tradingList, response.data[i].sysID)) {
            let record = {'sysID': response.data[i].sysID, 'action': response.data[i].action, 'price': response.data[i].price, 'amount': response.data[i].amount, 'qty': response.data[i].qty}
            Store.commit('setTradingList', record)
          }
        }
      }
    })
}
function getMyTradingFromWebApi (account) {
  // 从数据库加载交易记录至交易列表
  vm.$http.get('/api/EXTrade?account=' + account + '&token=' + Store.getters.getTokenSymbol, { emulateJSON: true })
    .then((response) => {
      // console.log('getMyTradingFromWebApi', response.data.length, Store.state.tokens.myTradingForTable.length, '\naccount', account)

      if (response.data.length > 0) {
        for (let i = response.data.length - 1; i > -1; i--) {
          // 检查sysID 是否重复
          if (!Global.getArrayListSysID(Store.state.tokens.myTradingList, response.data[i].sysID)) {
            let row = response.data[i]
            let record = {'sysID': row.sysID, 'token': row.token, 'time': row.time, 'account': row.account, 'type': 'load', 'action': row.action, 'price': row.price, 'amount': row.amount, 'qty': row.qty, 'myTokens': row.myTokens, 'myDividends': row.myDividends, 'status': row.status, 'hash': row.hash}
            Store.commit('setMyTradingList', record)
          }
        }
        // 复制一份用于表格显示
        Store.state.tokens.myTradingForTable = Store.state.tokens.myTradingList.slice(0)
      }
    })
}
// 获取静态信息
function getStaticInfo () {
  tokenContract.symbol()
    .then((result) => {
      let rest = result[0].toString()
      console.log('symbol: ' + rest)
      Store.commit('setTokenSymbol', rest)
      getTradingFromWebApi(rest)
    })

  tokenContract.name()
    .then((result) => {
      let rest = result[0].toString()
      Store.commit('setTokenName', rest)
    })

  if (Store.getters.getTokenSymbol === Store.getters.getSysTokenSymbol) {
    // 默认币种 seashells 数据（早期合约未提供getMetadata函数及部分功能; 需要单独设置）
    let pm = {
      'name': Store.getters.getTokenName,
      'symbol': Store.getters.getTokenSymbol,
      'memo': 'seashells',
      'tag': '1000000000',
      'purchaseLimit': '5000000',
      'dividendFee': 10,
      'referrerFee': 3,
      'ambassadorMaxPurchase': 1,
      'ambassadorQuota': 20,
      'onlyAmbassadors': false
    }
    Store.commit('setTokenMetadata', pm)
  } else {
    tokenContract.onlyAmbassadors()
      .then((result1) => {
        let rest1 = result1[0].toString()

        Store.commit('setOnlyAmbassadors', rest1)
        console.log('onlyAmbassadors', rest1, Store.getters.getOnlyAmbassadors)

        // 如果是天使阶段 检查当前帐号是否为股东
        if (rest1 === 'true') {
          // 设置天使帐号申购上限
          tokenContract.ambassadorMaxPurchase_()
            .then((result2) => {
              let rest2 = web3js.fromWei(result2[0].toString())
              Store.commit('setAmbassadorMaxPurchase', rest2)
              console.log('OnlyAmbassadors', rest1, '\n' + Store.getters.getCustomerAddress, '\nIsAmbassador', Store.getters.getIsAmbassador)
            })
          // 设置天使申购总额
          tokenContract.ambassadorQuota_()
            .then((result3) => {
              let rest3 = web3js.fromWei(result3[0].toString())
              Store.commit('setAmbassadorQuota', rest3)
              // console.log('setIsAmbassador', Store.state.isAmbassador, Store.getters.getCustomerAddress)
            })
        }
      })

    tokenContract.shareDiviesLowerLimit()
      .then((result) => {
        let rest = result[0].toString()
        Store.commit('setShareDiviesLowerLimit', rest)
      })

    tokenContract.getMetadata()
      .then((result) => {
        let name = result[0].toString()
        let symbol = result[1].toString()
        let memo = result[2].toString()
        let tag = result[3].toString()
        let purchaseLimit = web3js.fromWei(result[4].toString())
        let dividendFee = result[5].toString()
        let referrerFee = result[6].toString()
        let ambassadorMaxPurchase = web3js.fromWei(result[7].toString())
        let ambassadorQuota = web3js.fromWei(result[8].toString())
        let onlyAmbassadors = result[9].toString()
        let shareDiviesLowerLimit = ''
        if (typeof result[10] !== 'undefined') {
          shareDiviesLowerLimit = result[10].toString()
        }
        let pm = {
          'name': name,
          'symbol': symbol,
          'memo': memo,
          'tag': tag,
          'purchaseLimit': purchaseLimit,
          'dividendFee': dividendFee,
          'referrerFee': referrerFee,
          'ambassadorMaxPurchase': ambassadorMaxPurchase,
          'ambassadorQuota': ambassadorQuota,
          'onlyAmbassadors': onlyAmbassadors,
          'shareDiviesLowerLimit': shareDiviesLowerLimit
        }
        Store.commit('setTokenMetadata', pm)
      })
  }

  // 从区块链获取分红记录
  // getShareDividendRecord()
}

// 获取实时信息
function getDynamicInfo () {
  totalSupply()
  totalEthereumBalance()
  buyPrice()
  sellPrice()

  // 检查网络
  if (typeof window.web3 !== 'undefined') {
    // 检查帐号和网络

    if (window.web3.eth.coinbase !== null && typeof window.web3.eth.coinbase !== 'undefined' && Store.getters.getNetwork === Store.getters.getTokenHostNetwork) {
      let myAddress = window.web3.eth.coinbase
      let isChangeAccount = false
      if (Store.getters.getCustomerAddress !== myAddress) {
        // 如果更换帐号则先重置帐号数据
        console.log('account change', myAddress)
        isChangeAccount = true
        accountClear()
        getMyTradingFromWebApi(myAddress)
      }
      // console.log('setCustomerAddress1', window.web3.eth.coinbase, Store.getters.getCustomerAddress, Store.getters.getNetwork, Store.getters.getPresetNetwork)
      Store.commit('setCustomerAddress', myAddress)
      // 如果是天使阶段 检查当前帐号是否为股东
      if (Store.getters.getOnlyAmbassadors === 'true') {
        // 当前帐号已消费金额
        tokenContract.ambassadorAccumulatedQuota_(myAddress)
          .then((result2) => {
            let rest = web3js.fromWei(result2[0].toString())
            Store.commit('setAmbassadorAccumulatedQuota', rest)
          })
      }

      // 加载帐号数据

      // 当前帐号是否为天使
      tokenContract.ambassadors_(myAddress)
        .then((result3) => {
          let rest = result3[0].toString()
          Store.commit('setIsAmbassador', rest)
        })

      myTokens(myAddress, false)
      myDividends(1, myAddress, false, isChangeAccount)
    }
  }
  // 每12秒更新基础数据,用归递超时调用来模拟间歇调用，禁用setInterval
  setTimeout(function () {
    if (tokenContract !== null) {
      getDynamicInfo()
    }
  }, 1000 * 12)
}

