<template>
  <div class="background">
    <!--title-->
    <div class="excessive">
      <my-title/>
    </div>

    <div class="floatCenter">
    <div style="margin-top: 5rem">
      <div class="input_box">
        <div style="font-size: x-large">申请发行数字货币</div>
      </div>
      <div class="input_box" style="margin-top: 2rem">
        <div>公司 <span style="color: orangered;">*</span></div>
        <input class="input_text" type="text" maxlength="30" v-model="company"/>
      </div>
      <div class="input_box">
        <div>币种全称 <span style="color: orangered;">*</span></div>
        <input class="input_text" type="text" maxlength="30" v-model="tokenFullName"/>
      </div>
      <div class="input_box">
        <div>币种简称 <span style="color: orangered;">*</span><span class="inputTitleHint">英文字母 小于5个字</span></div>
        <input class="input_text" type="text" maxlength="10" v-model="token"/>
      </div>
      <div class="input_box">
        <div>联系人 <span style="color: orangered;">*</span></div>
        <input class="input_text" type="text" maxlength="30" v-model="linkMan"/>
      </div>
      <div class="input_box">
        <div>电话 <span style="color: orangered;">*</span></div>
        <input class="input_text" type="text" maxlength="30" v-model="telephone"/>
      </div>
      <div class="input_box">
        <div>其它联系方式</div>
        <input class="input_text" type="text" maxlength="30" v-model="otherContacts"/>
      </div>
      <div class="input_box">
        <div>官网</div>
        <input class="input_text" type="text" maxlength="150" placeholder='链接地址' style="width: 52rem" v-model="website"/>
      </div>
      <div class="input_box">
        <div>白皮书</div>
        <input class="input_text" type="text" maxlength="150" placeholder='链接地址' style="width: 52rem" v-model="whiteBook"/>
      </div>
      <div class="input_box">
        <div>项目介绍
          <span style="color: orangered;">*</span>
          <span class="inputTitleHint">大于300字 小于3000字 不能包含单引号</span>
          <span class="inputTitleHint">{{this.textLength}} / 3000</span>
        </div>
        <textarea class="input_text" maxlength="3000" @input="inputLength" style="height: 19rem; width: 52rem" type="text" v-model="introduce"/>
      </div>
      <div class="input_box" style="margin-top: 2rem">
        <button class="block_button" @click="apply">提交申请</button>
      </div>
    </div>
    </div>

      <!--boot-->
      <div class="excessive" style="margin-top: 2rem; padding-top: 3rem;padding-bottom: 2rem">
        <myFoot/>
      </div>

  </div>
</template>

<script>
import myTitle from '../components/TitleEx'
import myFoot from '../components/FootEx'

export default {
  name: 'apply',
  components: {myTitle, myFoot},
  data: function () {
    return {
      company: '',
      tokenFullName: '',
      token: '',
      linkMan: '',
      telephone: '',
      otherContacts: '',
      introduce: '',
      status: '申请',
      website: '',
      whiteBook: '',
      textLength: 0
    }
  },
  beforeCreate () {
    document.querySelector('body').setAttribute('style', 'margin: 0; padding:0;')
  },
  // 方法集合
  methods: {
    inputLength () {
      this.textLength = this.introduce.length
    },
    apply () {
      var error_ = ''
      if (this.company.length < 2) {
        error_ += '公司名称不能小于2个字'
      }
      if (this.tokenFullName.length < 3) {
        error_ += '\n币种全称不能小于3个字'
      }
      if (this.token.length < 2) {
        error_ += '\n币种简称不能小于2个字'
      }
      if (this.linkMan.length < 2) {
        error_ += '\n联系人不能小于2个字'
      }
      if (this.telephone.length < 5) {
        error_ += '\n电话不能小于5个字'
      }

      if (this.introduce.length < 300) {
        error_ += '\n项目介绍不能小于300个字'
      }
      // 当前提交文本内容有单引号 时不能转换为varchar类型 会报错 (0x80131904): 'post' 附近有语法错误
      if (this.introduce.indexOf("'") > 0) {
        error_ += '\n项目介绍内容不能含有 单引号'
      }

      if (error_.length < 1) {
        this.$http.get('/api/token/findToken?token=' + this.token, {emulateJSON: true})
          .then((response) => {
            // console.log('findToken', this.token, response.data)
            // 如果token已存且仍在申请状态 对话询问是否修改 否则新增
            if (response.data !== '') {
              if (response.data === '申请') {
                if (confirm('数字货币' + this.token + '已存在 是否重新申请?')) {
                  // this.updateToken()
                  // put 方法不被推荐 改用新增代替修改
                  this.addToken()
                }
              } else {
                alert('数字货币' + this.token + '已存在')
              }
            } else {
              this.addToken()
            }
          })
      } else {
        alert(error_)
      }
    },

    addToken () {
      this.$http.post('/api/token',
        {
          company: this.company,
          tokenFullName: this.tokenFullName,
          token: this.token,
          linkMan: this.linkMan,
          telephone: this.telephone,
          otherContacts: this.otherContacts,
          introduce: this.introduce.toString(),
          status: this.status,
          website: this.website,
          whiteBook: this.whiteBook,
          emulateJSON: true
        })
        .then((response) => {
          console.log('post', response.data)
          if (parseInt(response.data) > 0) {
            alert('您的申请已提交，近期会有工作人员与您联系 您也可以拨打服务电话查询进度')
          } else {
            alert('提交失败，稍后在试或拨打服务电话反馈')
          }
        }, (error) => {
          alert(error + '\n提交失败，稍后在试或拨打服务电话反馈')
        })
    },

    // put 方法不被推荐 改用新增代替修改
    updateToken () {
      this.$http.put('/api/token',
        {
          company: this.company,
          tokenFullName: this.tokenFullName,
          token: this.token,
          linkMan: this.linkMan,
          telephone: this.telephone,
          otherContacts: this.otherContacts,
          introduce: this.introduce,
          status: this.status,
          website: this.website,
          whiteBook: this.whiteBook,
          emulateJSON: true })
        .then((response) => {
          console.log('put', response.data)
          if (parseInt(response.data) > 0) {
            alert('您的修改申请已提交')
          } else {
            alert('提交失败，稍后在试或拨打服务电话反馈')
          }
        }, (error) => {
          alert(error + '\n提交失败，稍后在试或拨打服务电话反馈')
        })
    }
  },
  // 钩子函数 组件结束前执行
  beforeDestroy () {
    console.log(this.$options.name, 'log out')
    document.querySelector('body').removeAttribute('style')
  }
}
</script>

<style scoped>
  .floatCenter {
    margin: 0 auto;
    display: inline-block;
  }
  .background {
    text-align: center;
    margin: 0;
    padding: 0;
    border: 0;
    width: 100%;
    height: 100%;
    background-color: #E5EBF5;
  }
  .input_box {
    text-align:left;
    margin: 2rem
  }
  .inputTitleHint {
    margin-left: 1rem;
    color: green;
    font-size:small;
  }
  .input_text {
    margin: 0;
    padding: 0;
    height: 3rem;
    width: 29rem;
    font-size: 1rem;
    border: 1px solid #D8D8D8;
    border-image: none;
    text-indent: 0.2rem;
  }
  .block_button {
    width: 12rem;
    height: 2.5rem;
    font-size: large;
    background-color: #9980D6;
    color: rgba(250, 250, 250, 1);
    margin: 1rem auto 0;
    cursor: pointer;
  }
  .excessive {
    text-align: center;
    width: 100%;
    height: 100%;
    background: linear-gradient(to right, #B292FF, #E987C5);
  }

</style>



