import Vue from 'vue'
import Router from 'vue-router'
import FB from '@/components/FreeBlock.vue'
import test from '@/components/mytest.vue'
import home from '@/components/home.vue'
import apply from '@/components/apply.vue'
import token from '@/components/token.vue'
import seashells from '@/components/seashells.vue'
import SourceCode from '@/components/SourceCode.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: home
    },
    {
      path: '/token',
      name: 'token',
      component: token
    },
    {
      name: 'empty',
      path: 'tokenQuery'
      // redirect: '/token'
    },
    {
      path: '/seashells',
      name: 'seashells',
      component: seashells
    },
    {
      path: '/apply',
      name: 'apply',
      component: apply
    },
    {
      path: '/FreeBlock',
      name: 'FreeBlock',
      component: FB
    },
    {
      path: '/test',
      name: 'test',
      component: test
    },
    {
      path: '/SourceCode',
      name: 'SourceCode',
      component: SourceCode
    }
  ]
})



