<template>
  <div>
  <myTitle/>
  <div>
    <div>{{code1}}</div>
    <div v-text="code1"></div>
    <div v-html="code1"></div>
  </div>

  <myfoot></myfoot>
  </div>
</template>

<script>
import myTitle from '../components/Title'
import myFoot from '../components/Foot'
export default {
  name: 'SourceCode',
  components: {myTitle, myFoot},
  data () {
    return {
      code1:
      '      /**\n' +
      '      * @dev 两个数字的减法，如果减数大于被减数，则溢出抛出。\n' +
      '      */\n' +
      '      function sub(uint256 a, uint256 b) internal pure returns (uint256) {\n' +
      '      assert(b <= a);\n' +
      '      return a - b;\n' +
      '      }'
    }
  }
}
</script>

<style scoped>

</style>



