// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
// 注册全局组件
import Vue from 'vue'
import App from './App'
import router from './router'
import axios from 'axios'
import MuseUI from 'muse-ui'
import store from './js/store'
import global from './js/global'

// 全局公用style
import './styles/index.css'

import 'vue-easytable/libs/themes-base/index.css'
import {VPagination, VTable} from 'vue-easytable'

import Tooltip from 'vue-directive-tooltip'
import 'vue-directive-tooltip/css/index.css'
// 不要引用babel-polyfill 在IE下会报错

Vue.use(Tooltip)

Vue.use(MuseUI)

// const host = process.env.NODE_ENV === 'development' ? 'http://localhost:8080' : 'http://182.61.23.149:8089' // 根据 process.env.NODE_ENV 的值判断当前是什么环境
const host = process.env.NODE_ENV === 'development' ? 'https://abcai.org/exApi' : 'https://abcai.org/exApi' // 根据 process.env.NODE_ENV 的值判断当前是什么环境

const instance = axios.create({
  baseURL: host,
  withCredentials: false
})
Vue.prototype.$http = instance
Vue.prototype.$global = global

Vue.component(VTable.name, VTable)
Vue.component(VPagination.name, VPagination)

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  instance,
  store,
  template: '<App/>',
  components: { App }
})



