                <span class="helpBackground" style="float:left; margin-top: 1rem" v-tooltip.right ="{content:'分红金额（以太币）会平均分给所有数字货币\n\n用户所得金额以其手中所持有的数字货币数量乘每币所得计算'}"/>
              </div>
              <div style="margin: 0 auto; display: inline-block;">
                  <div style="font-family:Helvetica;text-align: left; margin-left: 3rem; margin-top: 3rem;">
