            <!--用户帐号权益-->
            <div style="width: 100%; height: 18rem; background-color: white; margin-top: 0.2rem;border:1px solid white;">
              <div style="margin-top: 1rem">我 的 收 益
                <span class="helpBackground" style="float:left; margin-left: 1.5rem; " v-tooltip.right ="{content:'登录METAMASK帐号查看当前帐号收益\n收益来源有交易返利，推荐奖励，卖出获利和分红四种,其中交易返利和分红由系统根据你的持币数量自动分配\n\n推荐奖励是由其它用户使用你的推荐连接交易而获得奖励\n推荐奖励和交易返利的获得比例可参考发行方的币种介绍\n\n收益复投是将您现有收益用于购买当前数字货币\n收益提取是将您现有收益提取到您当前的以太坊帐号中\n收益复投和提取均会弹出GAS确认框', offset: 10 }"/>
              </div>
              <div style="text-align: left; font-family:serif; margin:1rem 1rem 1rem 3rem;">
