                <div>
                  <span style="width: 15rem;">我的数字货币: {{this.$store.getters.getMyTokens|numFilterFixed4}}</span>
                  <span style="margin-left: 8rem; width: 12rem">我的收益: {{this.$store.getters.getMyDividends}} eth</span>
                  <span style="margin-left: 8rem; width: 12rem">持币比例: {{calculateBubbleratio}} %</span>
                </div>
                <div style="margin-top: 1rem">
                  <button class="block_button" @click="reinvest">收 益 复 投</button>
                  <button class="block_button" style="margin-left: 6rem" @click="withdraw">收 益 提 现</button>
                </div>
              </div>
            </div>
          </div>

          <div style="width:100%; background-color: white; height: 25rem">
            <div style="width: 100%; margin-top: 0.1rem;">
              <div style="padding-top:2rem;">
                我的交易记录
                <span class="helpBackground" style="float:left; margin-left: 1.5rem; margin-top: 0.5rem" v-tooltip.right ="{content:'显示当前帐号的交易记录\n动态收益即股息 是来自该合约的交易返利或推荐奖励'}"/>
                <div>
                  <v-table
                    style="width: 100%;height: 17rem;margin-top: 1rem"
                    is-horizontal-resize
