                    title-bg-color="#EEF6FD"
                    row-click-color="#EEF6FD"
                    :paging-index="(pageIndex-1)*pageSize">
                  </v-table>
                  <v-pagination
                    style="margin-top: 0.2rem"
                    @page-change="pageChange"
                    :total="this.$store.state.tokens.myTradingLengthForTable" :page-size="pageSize"
                    :layout="['total', 'prev', 'pager', 'next', 'jumper']"></v-pagination>
                </div>
