              <div style="text-align: left; padding: 2rem">
                <!--<img src="/static/logo/abc150.png" style="width: 5rem; height: 5rem"/>-->
                <img v-show="tokenLogo!=null" :src=tokenLogo style="width: 3.5rem; height: 3.5rem"/>
                <div style="margin-top: 1rem">
                  <span style="width: 25rem; display: inline-block;">发行方： {{this.$store.state.tokens.tokenProject.company}} </span>
                  <span style="width: 15rem; display: inline-block;">币种名称：{{this.$store.state.tokens.tokenMetadata.name}} </span>
                  <span style="width: 15rem; display: inline-block;">标签：{{this.$store.state.tokens.tokenMetadata.symbol}} </span>
                </div>
                <div style="margin-top: 1rem">
                  <span style="width: 25rem; display: inline-block;">限购总额：{{purchaseLimitShow}} <span>万</span> </span>
                  <span style="width: 15rem; display: inline-block;">股东限购：{{this.$store.state.tokens.tokenMetadata.ambassadorMaxPurchase}} <span>eth</span> </span>
                  <span style="width: 15rem; display: inline-block; v-if()">状态：{{contractStatusShow}}</span>
                </div>
                <div style="margin-top: 1rem">
                  <span style="width: 25rem; display: inline-block;">编号：{{this.$store.state.tokens.tokenMetadata.tag}}</span>
                  <span style="width: 15rem; display: inline-block;">交易返利：{{dividendFeeShow}} <span>%</span></span>
                  <span style="width: 15rem; display: inline-block;">推荐奖励：{{referrerFeeShow}} <span>%</span></span>
                </div>
                <div style="margin-top: 1rem">
                  <a :href=this.$store.state.tokens.tokenProject.website target="_blank">
                  <span style="width: 25rem; display: inline-block;">官网：{{this.$store.state.tokens.tokenProject.website}}</span>
                  </a>
                  <!--文本超出显示...overflow: hidden; text-overflow: ellipsis -->
                  <a :href=this.$store.state.tokens.tokenProject.whiteBook target="_blank">
                  <span style="width: 37rem; display:inline-block; white-space: nowrap; overflow: hidden; text-overflow: ellipsis">白皮书：{{this.$store.state.tokens.tokenProject.whiteBook}}</span>
                  </a>
                </div>
                <div style="margin-top: 1rem;">
                  <span>简介：</span>
                  <!--本方插值换行white-space: pre-wrap-->
                  <div style="margin-top: 1rem; white-space: pre-wrap" v-html="this.$store.state.tokens.tokenProject.introduce"></div>
                </div>
            </div>
          </div>
        </div>
      </div>
    <!--boot-->
    <div class="excessive" style="padding-top: 2rem; padding-bottom: 5rem; border-top: 1px solid white;">
      <myFoot/>
    </div>
  </div>
</template>

<script>
import myTitle from '../components/TitleEx'
import myFoot from '../components/FootEx'
import tokenHeadVM from './tokenHead'
import tokenHeadjs from '../js/tokenHead'
import tokenBasejs from '../js/tokenBase'
import router from '@/router/index.js'
export default {
  name: 'tokenBody',
  components: {myTitle, myFoot, tokenHeadVM},
  data: function () {
    return {
      buyInput: '',
      sellInput: '',
      dividendInput: '',
      dividendPerShare: 0,
      tokens: ['SS', 'WPR', 'EVX', 'COVA', 'LAMB', 'FTI'], // 全部token列表, 新token需要在此添加symbol 用于边栏显示token价格动态
      sellPriceIndexShow: ['sell9', 'sell8', 'sell7', 'sell6', 'sell5', 'sell4', 'sell3', 'sell2', 'sell1'], // 卖价序号显示字符
      buyPriceIndexShow: ['buy9', 'buy8', 'buy7', 'buy6', 'buy5', 'buy4', 'buy3', 'buy2', 'buy1'], // 买价序号显示字符
      pageIndex: 1,
      pageSize: 6,
      myTradingRecord: {
        tableData: [],
        tableColumns: [
          {field: 'time', title: '时间', titleAlign: 'center', columnAlign: 'center', width: 120, isResize: true},
          {field: 'token', title: '币种', titleAlign: 'center', columnAlign: 'center', width: 60, isResize: true},
          {field: 'action', title: '操作', titleAlign: 'center', columnAlign: 'center', width: 60, isResize: true},
          {field: 'price', title: '价格(ETH)', titleAlign: 'center', columnAlign: 'center', width: 60, isResize: true},
          {field: 'amount',
            title: '交易金额(ETH)',
            titleAlign: 'center',
            columnAlign: 'center',
            width: 60,
            isResize: true,
            formatter:
              function (rowData, rowIndex, pagingIndex, field) {
                return rowData.amount > 0 ? Math.round(rowData.amount * 10000) / 10000 : ''
              }
          },
          {field: 'qty', title: '交易数量', formatter: function (rowData, rowIndex, pagingIndex, field) { return rowData.qty > 0 ? Math.round(rowData.qty * 10000) / 10000 : '' }, titleAlign: 'center', columnAlign: 'center', width: 60, isResize: true},
          {field: 'myTokens', title: '我的货币', formatter: function (rowData, rowIndex, pagingIndex, field) { return rowData.myTokens > 0 ? Math.round(rowData.myTokens * 10000) / 10000 : '' }, titleAlign: 'center', columnAlign: 'center', width: 60, isResize: true},
          {field: 'myDividends', title: '我的收益(ETH)', formatter: function (rowData, rowIndex, pagingIndex, field) { return rowData.myDividends > 0 ? Math.round(rowData.myDividends * 10000) / 10000 : '' }, titleAlign: 'center', columnAlign: 'center', width: 80, isResize: true},
          {
            field: 'status',
            title: '状态',
            titleAlign: 'left',
            columnAlign: 'left',
            width: 50,
            isResize: true,
            formatter: function (rowData) {
              let res = ''
              switch (rowData.status) {
                case 'PENDING':
                  res = '进行中'
                  break
                case 'CONFIRM':
                  res = '完成'
                  break
                case 'REJECT':
                  res = '取消'
                  break
                case '+':
                  res = '增加'
                  break
                case '-':
                  res = '减少'
                  break
              }
              return res
            }
          }
        ]
      }
    }
  },
  computed: {
    tokenLogo: function () {
      return '/static/logo/' + this.$store.getters.getTokenSymbol + '.png'
    },
    contractStatusShow: function () {
      if (this.$store.getters.getOnlyAmbassadors === 'true') {
        return '天使阶段'
      } else {
        return '上市'
      }
    },
    showIsAmbassador: function () {
      let res = '否'
      if (this.$store.getters.getIsAmbassador === 'true') {
        res = '是'
      }
      return res
    },
    // 计算持币比例
    calculateBubbleratio: function () {
      let res = 0
      if (this.$store.getters.getMyTokens > 0 && this.$store.getters.getTotalToken > 0) {
        res = (parseFloat(this.$store.getters.getMyTokens) / parseFloat(this.$store.getters.getTotalToken)) * 100
        res = Math.round(res * 10000) / 10000
      }
      return res
    },
    purchaseLimitShow: function () {
      let res = ''
      if (this.$store.state.tokens.tokenMetadata.purchaseLimit > 0) {
        res = Math.round(this.$store.state.tokens.tokenMetadata.purchaseLimit / 10000)
      }
      return res
    },
    dividendFeeShow: function () {
      let res = 0
      if (this.$store.state.tokens.tokenMetadata.dividendFee > 0) {
        res = Math.round((100 / this.$store.state.tokens.tokenMetadata.dividendFee) * 100) / 100
      }
      return res
    },
    referrerFeeShow: function () {
      let res = 0
      if (this.$store.state.tokens.tokenMetadata.referrerFee > 0) {
        let dividenFee = Math.round((100 / this.$store.state.tokens.tokenMetadata.dividendFee) * 100) / 100
        let referrerFeeBydividendFee = Math.round((this.$store.state.tokens.tokenMetadata.referrerFee) * 100) / 100
        res = Math.round((((100 * dividenFee) / referrerFeeBydividendFee) / 100) * 100) / 100
      }
      return res
    }
  },
  filters: {
    // 保留4位小数 用于文本插值
    numFilterFixed4 (value) {
      let rest = Math.round(value * 10000) / 10000
      return rest
    }
  },
  watch: {
    // 同一页面不同参数时刷新页面 用于左侧token列表
    // '$route' () {
    //   this.$router.go(0)
    // },
    buyInput: function () {
      // 正4位小数的
      this.buyInput = (this.buyInput.match(/^\d*(\.?\d{0,4})/g)[0]) || null
    },
    sellInput: function () {
      // if (this.sellInput.substring(0, 1) !== '0') { // 不能以0开关
      //   this.sellInput = this.sellInput.replace(/[^\d]/g, '') // 只接整形数字
      // } else {
      //   this.sellInput = ''
      // }

      // 正8位小数的
      this.sellInput = (this.sellInput.match(/^\d*(\.?\d{0,4})/g)[0]) || null
    },
    dividendInput: function () {
      this.dividendInput = (this.dividendInput.match(/^\d*(\.?\d{0,4})/g)[0]) || null
      if (this.$store.getters.getTotalToken > 0 && this.dividendInput > 0) {
        this.dividendPerShare = this.dividendInput / this.$store.getters.getTotalToken
      }
    }
  },
  beforeCreate () {
    document.querySelector('body').setAttribute('style', 'margin: 0; padding:0;')
  },
  // 当前路由参数改变时重新加载数据
  beforeRouteUpdate (to, from, next) {
    if (to.name === from.name && to.query !== from.query) {
      let token_, by_
      token_ = to.query.token
      this.initTokenData(token_, by_)
      next()
    } else {
      next()
    }
  },
  mounted () {
    // http://localhost:8080/tokenBody?token=SS2&ref=0x342d343eF....
    let token_, by_
    // 加载当前页的token
    if (typeof this.$route.query.token !== 'undefined') {
      token_ = this.$route.query.token
    } else {
      // 如果没有token参数就用系统默认token
      token_ = this.$store.getters.getSysTokenSymbol
    }
    // 加载推荐者
    if (typeof this.$route.query.by !== 'undefined' && window.web3.isAddress(this.$route.query.by)) {
      by_ = this.$route.query.by
    }

    if (tokenHeadjs.checkExpress() === 1) {
      // 加载合约数据
      tokenHeadjs.initContract(this.tokens) // 获取tokens例表信息

      this.initTokenData(token_, by_)
    }

    console.log('mounted', token_, by_)
    // 加载token项目信息（来自服务器）
    this.getTokenProject(token_)
    // 加载公告
    this.getNotice(token_)
  },
  // 方法集合
  methods: {
    initTokenData (token, refBy) {
      // 清理 帐号数据
      tokenBasejs.accountClear()
      // 清理 币种数据
      tokenBasejs.tokenClear()

      // 加载当前页的token
      if (typeof token !== 'undefined') {
        this.$store.commit('setTokenSymbol', token)
      } else {
        this.$store.commit('setTokenSymbol', this.$store.getters.getSysTokenSymbol)
      }

      // 加载推荐者
      if (typeof refBy !== 'undefined' && tokenBasejs.web3js.isAddress(refBy)) {
        this.$store.commit('setReferredBy', refBy)
      }

      console.log(this.$options.name, 'log in token : ', token, 'referredBy', refBy)

      tokenBasejs.initContract(this.$store.getters.getTokenSymbol) // 获取当前页主体token信息
    },
    tokenHeadListonClick (token) {
      this.$router.push({path: '/token', query: {token: token}})

      // 加载token项目信息（来自服务器）
      this.getTokenProject(token)
      // 加载公告
      this.getNotice(token)
    },
    getTradingTableData () {
      // // 从本地仓库中加载用户交易记录, 用于表格
      // let localTradingList = []
      // for (var i = 0; i < localStorage.length; i++) {
