      //         // 清空表格数据 重新加载
      //         this.$store.state.tokens.myTradingForTable.splice(0, this.$store.state.tokens.myTradingForTable.length)
      //         console.log('getTradingTableData1', response.data.length)
      //         for (let i = 0; i < response.data.length; i++) {
      //           let row = response.data[i]
      //           let record = {'sysID': row.sysID, 'token': row.token, 'time': row.time, 'account': row.account, 'type': 'load', 'action': row.action, 'price': row.price, 'amount': row.amount, 'qty': row.qty, 'status': row.status, 'hash': row.hash}
      //           this.$store.commit('setMyTradingForTable', record)
      //         }
      //       }
      //     })

      // console.log('getTradingTableData', this.$store.state.tokens.myTradingList.length, this.$store.state.tokens.myTradingForTable.length)
      if (this.$store.state.tokens.myTradingList.length > 0) {
        // this.$store.state.tokens.myTradingList.sort(function (a, b) {
        //   return b.sysID - a.sysID
        // })
        // this.$store.state.tokens.myTradingList.sort()
        this.$store.state.tokens.myTradingForTable = this.$store.state.tokens.myTradingList.slice(
          (this.pageIndex - 1) * this.pageSize, this.pageIndex * this.pageSize)
        this.$store.state.tokens.myTradingLengthForTable = this.$store.state.tokens.myTradingList.length
      }
    },
    columnCellClass (rowIndex, columnName, rowData) {
      if (rowData.amount > 10) {
        return 'bets-isget-false'
      }
      if (columnName === 'action') {
        return 'bets-isget-false'
      }
      if (rowData.action === '分红' && columnName === 'action') {
        return 'bets-winA-GreaterThanZero'
      }
    },
    pageChange (pageIndex) {
      this.pageIndex = pageIndex
      this.getTradingTableData()
    },
    calculateTokensReceived () {
      var inputValue = this.buyInput
      tokenBasejs.calculateTokensReceived(inputValue)
    },
    // 动态获取卖出订单价格
    calculateEthereumReceived () {
      var inputValue = this.sellInput
      tokenBasejs.calculateEthereumReceived(inputValue)
    },
    Buy () {
      var buyQty = this.buyInput
      tokenBasejs.buy(buyQty)
    },
    Sell () {
      var sellQty = this.sellInput
      tokenBasejs.sell(sellQty)
    },
    getTokenProject (tokenSymbol) {
      // let token = this.$store.getters.getTokenSymbol
      this.$http.get('/api/token?token=' + tokenSymbol, {emulateJSON: true})
        .then((response) => {
          if (response.data.length > 0) {
            let tokenProject = {
              'sysID': response.data[0].sysID,
              'sysTime': response.data[0].sysTime,
              'company': response.data[0].company,
              'tokenFullName': response.data[0].tokenFullName,
              'token': response.data[0].token,
              'linkMan': response.data[0].linkMan,
              'telephone': response.data[0].telephone,
              'otherContacts': response.data[0].otherContacts,
              'introduce': response.data[0].introduce,
              'status': response.data[0].status,
              'tag': response.data[0].tag,
              'website': response.data[0].website,
              'whiteBook': response.data[0].whiteBook,
              'listedTime': response.data[0].listedTime
            }
            this.$store.commit('setTokenProject', tokenProject)
          }
        })
    },
    getNotice (tokenSymbol) {
      this.$http.get('/api/EXNotice?initiator=' + tokenSymbol, {emulateJSON: true})
        .then((response) => {
          if (response.data.length > 0) {
            for (let i = 0; i < response.data.length; i++) {
              let notice = {'sysID': response.data[i].sysID, 'sysTime': response.data[i].sysTime, 'title': response.data[i].title, 'initiator': response.data[i].initiator, 'content': response.data[i].content}
              this.$store.commit('setNoticeList', notice)
            }
          }
        })
      // tokenBasejs.getNotice()
    },
    test1 () {
      // http://182.61.23.149/WebService/WebService2.asmx/versionUpdate
      // https://api.douban.com//v2/movie/top250
      // http://jsonplaceholder.typicode.com/users

      // this.$http.post('/api/token',
      //   { company: 'comp4', project: 'proj4', token: 'token3', linkMan: 'lm3', telephone: 'telp3', otherContacts: 'oc3', introduce: 'intr3', status: 'st3', emulateJSON: true })
      //   .then((response) => {
      //     console.log('post', response)
      //   })
      //
      // this.axios.put('/api/token',
      //   { company: 'comp4up', project: 'proj4up', token: 'token3', linkMan: 'lm3', telephone: 'telp3', otherContacts: 'oc3', introduce: 'intr3', memo: 'memo3', status: 'st3', tag: '4444', website: 'website3', whiteBook: 'whiteBook3', listedTime: '2019-02-07 18:41:00', emulateJSON: true })
      //   .then((response) => {
      //     console.log('put', response)
      //   })
      //
      // this.$http.get('/api/token?status=申请', {emulateJSON: true}, {headers: {
      //   'Content-Security-Policy': 'upgrade-insecure-requests',
      //   'Access-Control-Allow-Origin': 'https://abcai.org/webApi',
      //   'Access-Control-Allow-Headers': 'X-Requested-With,Content-Type',
      //   'Access-Control-Allow-Methods': 'PUT,POST,GET,DELETE,OPTIONS'
      // }})
      //   .then((response) => {
      //     console.log('response', response, response.data[0].token, response.data[0].status)
      //   })
      this.$http.get('/api/token?status=申请', {emulateJSON: true})
        .then((response) => {
          console.log('response', response, response.data[0].token, response.data[0].status)
        })
    },
    test2 () {
      this.$http.get('/api/token?status=申请', {emulateJSON: true})
        .then((response) => {
          console.log('response', response, response.data[0].token, response.data[0].status)
        })
    },
    // 将所有的红利再投资为代币。包括股息和分红
    reinvest () {
      tokenBasejs.reinvest()
    },
    /**
       * 取走请求者的所有收益。
       */
    withdraw () {
      tokenBasejs.withdraw()
    },
    dividends () {
      if (this.$store.getters.getTotalToken > 0 && this.dividendInput > 0) {
        let amount = this.dividendInput
        tokenBasejs.shareDividend(amount)
      } else {
        alert('当前数字货币总量需大于0\n当前输入金额需大于0')
      }
    },
    getShareDividendRecord () {
      tokenBasejs.getShareDividendRecord()
    },
    forInput () {
      let pm = this.inputText
      tokenBasejs.forInput(pm)
    },
    goTokenHeadLink (tokenSymbol) {
      console.log('tokenLink', tokenSymbol)
      router.push({name: 'token', query: {token: tokenSymbol}})
    },
    test () {
      // let localStorageKey = 'TradingRecord' + new Date().getTime().toString()
      // let record = {'time': new Date().getTime(), 'address': 'add1111', 'type': 'TradingRecord', 'action': 'buy', 'amount': 'ddd44', 'state': 'pending'}
      // localStorage.setItem(localStorageKey, JSON.stringify(record)) // 保存交易记录
      for (var i = 0; i < localStorage.length; i++) {
        var key = localStorage.key(i)
        if (key.indexOf('TradingRecord') > -1) {
          let keyValue = JSON.parse(localStorage.getItem(key))
          console.log('record', keyValue.time)
        }
      }
      // console.log('record', localStorage.getItem('myTradingRecord'), 'rel', localStorage.length)
    }
  },

  // 钩子函数 组件结束前执行
  beforeDestroy () {
    console.log(this.$options.name, 'log out')
    document.querySelector('body').removeAttribute('style')
  }
}
</script>

<!--本地样式-->
<style scoped>
  .floatCenter {
    width: 100%;
    height: 100%;
    margin: 0 auto;
    display: inline-block;
  }
  .background {
    text-align: center;
    width: 100%;
    height: 100%;
    background-color: #E5EBF5;
  }

  .liByNotice {
    text-align: left;
    list-style-type: none;
    width: 100%;
    border-radius: 0.2rem;
    background-color: white;
  }

  .liByTrading {
    list-style-type: none;
    text-align: left;
    margin-top: 0.5rem;
    height: 0.8rem;
    width: 100%;
  }
  .liItemPriceTrading {
    display: inline-block;
    width: 7rem;
    float: left;
    text-align: left;
  }
  .liItemTrading {
    display: inline-block;
    float: left;
    width: 3.3rem;
    text-align: left;
    margin-left: 1rem
  }
  .liByPrice {
    list-style-type: none;
    margin-top: 0.2rem;
    margin-left: 5rem;
    font-size: medium;
    height: 2rem;
    width: 13rem;
  }
  .liIndex {
    display: inline-block;
    width: 4rem;
    float: left;
    text-align: left;
  }
  .liItem {
    display: inline-block;
    width: 8rem;
    float: left;
    text-align: left;
    margin-left: 0.1rem;
  }

  .block_button {
    width: 15rem;
    height: 3rem;
    font-size: large;
    background: linear-gradient(to right, #9980D6, #E987C5);
    color: rgba(250, 250, 250, 1);
    border-radius: 2rem;
    margin-top: 1rem;
    cursor: pointer;
  }

  .input-text {
    margin: 0;
    padding: 0;
    height: 3rem;
    width: 13rem;
    font-size: 1rem;
    border: 1px solid #D8D8D8;
    border-image: none;
    text-indent: 0.2rem;
  }

  .input-submit {
    margin: 0;
    padding: 0;
    height: 2rem;
    width: 13rem;
    font-size: 1rem;
    background-image: linear-gradient(to bottom, #FBFBFB, #F3F3F3);
    border: 1px solid #D8D8D8;
    cursor: help;
    -moz-appearance: none;
    text-align: left;
    text-indent: 0.2rem;
  }
  .dividend-input {
    margin: 0;
    padding: 0;
    font-size: 1rem;
    cursor: pointer;
    height: 3rem;
    width: 35rem;
    -moz-appearance: none;
    text-align: left;
    text-indent: 0.2rem;
    float: left;
  }
  .dividend-submit {
    margin: 0;
    padding: 0;
    font-size: 1rem;
    background-color: #FF6B4A;
    border: 1px solid #FF6B4A;
    cursor: pointer;
    height: 3rem;
    width: 15rem;
    color: white;
    -moz-appearance: none;
    text-align: left;
    text-indent: 0.2rem;
    float: left;
  }
  .excessive {
    text-align: center;
    width: 100%;
    height: 100%;
    background: linear-gradient(to right, #9980D6, #E987C5);
  }
  .helpBackground{
    background-image:url('/static/pic/help.svg');
    background-size: cover;
    border: none;
    display: inline-block;
    width: 1.2rem;
    height: 1.2rem;
    cursor: help;
  }

</style>



