const serverPath = 'http://127.0.0.1:8080'
localStorage.setItem('10', '#41B883')
localStorage.setItem('11', '#727272')
const sysType = 10 // 系统类型本系统为10开头（以1开始的两位数）
const myColor = [10, 11] // 记录系统所用全部颜色
const geneBody = [10]
const genePattrn = [10, 11, 12, 13, 14, 15]
const geneHair = [10, 11]
const geneLeftEar = [10, 11]
const geneRightEar = [10, 11, 12]

const pending = false

// 检查浏览器
function checkExpress () {
  let ok = 0
  if (navigator.userAgent.indexOf('Firefox') >= 0) {
    ok = 1
  } else if (navigator.userAgent.indexOf('Opera') >= 0) {
    ok = 1
  } else if (navigator.userAgent.indexOf('Chrome') >= 0) {
    ok = 1
  } else if (navigator.userAgent.indexOf('Brave') >= 0) {
    ok = 1
  }
  console.log('express:' + ok)

  return ok
}

// 时间戳转为时间  格式'yyyy-MM-dd hh:mm:ss'
function getFormatDate (_pm1, _format) {
  if (!_pm1) {
    return ''
  }
  // 如果时间戳后不是毫秒值就加000,变成毫秒值
  if (_pm1 && _pm1.length === 10) {
    _pm1 = parseInt(_pm1 + '000')
  }

  var date = new Date(_pm1)
  var o = {
    'M+': date.getMonth() + 1, // 月份
    'd+': date.getDate(), // 日
    'h+': date.getHours(), // 小时
    'm+': date.getMinutes(), // 分
    's+': date.getSeconds(), // 秒
    'q+': Math.floor((date.getMonth() + 3) / 3), // 季度
    'S': date.getMilliseconds() // 毫秒
  }
  if (/(y+)/.test(_format)) {
    _format = _format.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length))
  }
  for (var k in o) {
    if (new RegExp('(' + k + ')').test(_format)) {
      _format = _format.replace(RegExp.$1, (RegExp.$1.length === 1) ? (o[k]) : (('00' + o[k]).substr(('' + o[k]).length)))
    }
  }
  return _format
}

function getCurrentTime () {
  var date = new Date()
  var year = date.getFullYear()
  /* 在日期格式中，月份是从0开始的，因此要加0
  * 使用三元表达式在小于10的前面加0，以达到格式统一  如 09:11:05
  */
  var month = date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1
  var day = date.getDate() < 10 ? '0' + date.getDate() : date.getDate()
  var hours = date.getHours() < 10 ? '0' + date.getHours() : date.getHours()
  var minutes = date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes()
  var seconds = date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds()
  // 拼接
  return year + '-' + month + '-' + day + ' ' + hours + ':' + minutes + ':' + seconds
}

function getTimeOf (time) {
  var date = new Date(time)
  var year = date.getFullYear()
  /* 在日期格式中，月份是从0开始的，因此要加0
  * 使用三元表达式在小于10的前面加0，以达到格式统一  如 09:11:05
  */
  var month = date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1
  var day = date.getDate() < 10 ? '0' + date.getDate() : date.getDate()
  var hours = date.getHours() < 10 ? '0' + date.getHours() : date.getHours()
  var minutes = date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes()
  var seconds = date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds()
  // 拼接
  return year + '-' + month + '-' + day + ' ' + hours + ':' + minutes + ':' + seconds
}

// 查询数组中的sysID 值是否存在
function getArrayListSysID (arrayList, sysID) {
  if (arrayList.length > 0) {
    for (let i = 0; i < arrayList.length; i++) {
      if (arrayList[i].sysID === sysID) {
        return true
      }
    }
  }
  return false
}

function createRandomGene () {
  // Math.floor(Math.random() * max) + 2 // 从2到max的随机数
  var maxColor = myColor.length
  var BodyID = Math.floor(Math.random() * geneBody.length) + 1
  var Body = geneBody[BodyID - 1]
  var bodyColorID = Math.floor(Math.random() * maxColor) + 1
  var bodyColor = myColor[bodyColorID - 1]
  var PattrnID = Math.floor(Math.random() * genePattrn.length) + 1
  var Pattrn = genePattrn[PattrnID - 1]
  var PattrnColorID = Math.floor(Math.random() * maxColor) + 1
  var PattrnColor = myColor[PattrnColorID - 1]
  var hairID = Math.floor(Math.random() * geneHair.length) + 1
  var hair = geneHair[hairID - 1]
  var LeftEarID = Math.floor(Math.random() * geneLeftEar.length) + 1
  var LeftEar = geneLeftEar[LeftEarID - 1]
  var rightEarID = Math.floor(Math.random() * geneRightEar.length) + 1
  var rightEar = geneRightEar[rightEarID - 1]
  var res = sysType.toString() + Body.toString() + bodyColor.toString() +
    Pattrn.toString() + PattrnColor.toString() +
    hair.toString() +
    LeftEar.toString() + rightEar.toString()
  return res
}

// 根据父母基因产生新的基因
function createGeneOfParents (_motherGene, _sire) {
  var i = sysType.toString().length // gene起始位数 增量为2
  var newgene = sysType.toString() // gene起始字符
  var maxColor = myColor.length
  // body0-2
  var motherBody = _motherGene.substr(i, 2)
  var sireBody = _sire.substr(i, 2)
  var randomBodyID = Math.floor(Math.random() * geneBody.length) + 1
  var randomBody = geneBody[randomBodyID - 1]
  var BodySet = [motherBody, sireBody, randomBody]
  var newBodyID = Math.floor(Math.random() * BodySet.length) + 1
  newgene = newgene + BodySet[newBodyID - 1].toString()
  // bodyColor 2-4
  i = i + 2
  var motherBodyColor = _motherGene.substr(i, 2)
  var sireBodyColor = _sire.substr(i, 2)
  var randomBodyColorID = Math.floor(Math.random() * maxColor) + 1
  var randomBodyColor = myColor[randomBodyColorID - 1]
  var BodyColorSet = [motherBodyColor, sireBodyColor, randomBodyColor]
  var newBodyColorID = Math.floor(Math.random() * BodyColorSet.length) + 1
  newgene = newgene + BodyColorSet[newBodyColorID - 1].toString()
  // pattrn4-6
  i = i + 2
  var motherPattrn = _motherGene.substr(i, 2)
  var sirePattrn = _sire.substr(i, 2)
  var randomPattrnID = Math.floor(Math.random() * genePattrn.length) + 1
  var randomPattrn = genePattrn[randomPattrnID - 1]
  var PattrnSet = [motherPattrn, sirePattrn, randomPattrn]
  var newPattrnID = Math.floor(Math.random() * PattrnSet.length) + 1
  newgene = newgene + PattrnSet[newPattrnID - 1].toString()
  // pattrnColor 6-8
  i = i + 2
  var motherPattrnColor = _motherGene.substr(i, 2)
  var sirePattrnColor = _sire.substr(i, 2)
  var randomPattrnColorID = Math.floor(Math.random() * maxColor) + 1
  var randomPattrnColor = myColor[randomPattrnColorID - 1]
  var pattrnColorSet = [motherPattrnColor, sirePattrnColor, randomPattrnColor]
  var newPattrnColorID = Math.floor(Math.random() * pattrnColorSet.length) + 1
  newgene = newgene + pattrnColorSet[newPattrnColorID - 1].toString()
  // hair8-10
  i = i + 2
  var motherHair = _motherGene.substr(i, 2)
  var sireHair = _sire.substr(i, 2)
  var randomHairID = Math.floor(Math.random() * geneHair.length) + 1
  var randomHair = geneHair[randomHairID - 1]
  var HairSet = [motherHair, sireHair, randomHair]
  var newHairID = Math.floor(Math.random() * HairSet.length) + 1
  newgene = newgene + HairSet[newHairID - 1].toString()
  // leftEar10-12
  i = i + 2
  var motherLeftEar = _motherGene.substr(i, 2)
  var sireLeftEar = _sire.substr(i, 2)
  var randomLeftEarID = Math.floor(Math.random() * geneLeftEar.length) + 1
  var randomLeftEar = geneLeftEar[randomLeftEarID - 1]
  var LeftEarSet = [motherLeftEar, sireLeftEar, randomLeftEar]
  var newLeftEarID = Math.floor(Math.random() * LeftEarSet.length) + 1
  newgene = newgene + LeftEarSet[newLeftEarID - 1].toString()
  // rightEar12-14
  i = i + 2
  var motherRightEar = _motherGene.substr(i, 2)
  var sireRightEar = _sire.substr(i, 2)
  var randomRightEarID = Math.floor(Math.random() * geneRightEar.length) + 1
  var randomRightEar = geneRightEar[randomRightEarID - 1]
  var rightEarSet = [motherRightEar, sireRightEar, randomRightEar]
  var newRightEarID = Math.floor(Math.random() * rightEarSet.length) + 1
  newgene = newgene + rightEarSet[newRightEarID - 1].toString()
  return 'm' + motherBody + 's' + sireBody + 'r' + randomBody + 'n' + newgene
  // return newgene + 'm' + rightEyeColorSet[0] + 'f' + rightEyeColorSet[1] + 'r' + rightEyeColorSet[2]
}
export default {
  serverPath, myColor, pending, checkExpress, getFormatDate, getCurrentTime, getTimeOf, getArrayListSysID, createRandomGene, createGeneOfParents
}



