<template>
  <div class="white">
    <div style="width: 80%; height: 100%; margin: 0 auto;background-color:#6495ED">
      <div id="show" class="title">
        <my-title/>
      </div>

      <!--part1-->
      <div style="margin-top: 29px;">
        <div class="fontLinght_short" style="font-size: xx-large">THE SS HAD STARTED</div>
        <div CLASS="fontLinght_short" style="font-size: x-large; margin-top: 19px">seashells earns you passive ETH dividends from its own volume, and all our games!</div>
        <div style="font-size: xx-large; margin-top: 19px">DAPP CENTER OF WORLD</div>
      </div>

      <!--part2-->
      <div style="padding-top:29px; width: 92%; margin:auto">
        <!--Overall Volume-->
        <div id="div1" class="part2_div_block" style="background-color: rgba(240,0,240, 1)" @click="goss">
          <div class="fontLinght_short" style="margin:5px; font-size: 2rem">seashells</div>
          <div class="block_text">Total tokens: {{this.$store.getters.totalTokens}} ss</div>
          <div class="block_text">buy price: {{this.$store.getters.buyPrice}} eth</div>
          <div class="block_text">sell price: {{this.$store.getters.sellPrice}} eth</div>
          <div class="block_text">10% of all volume the seashells token experiences, is taxxed and split across all seashells owners as passive eth  income.</div>
        </div>
        <!--Dividends-->
        <div id="div2" class="part2_div_block" style="background-color: rgba(100,149,237, 1)">
          <div class="block_text">Dividends</div>
          <div class="block_text">Total paid out as dividends by the smart-contract</div>
          <div class="block_text" style="margin-top: 9px; margin-bottom: 9px">totalDividends</div>
          <div class="block_text">The smart contract handles payouts, never humans.</div>
        </div>
        <!--All-Time Averages-->
        <div id="div3" class="part2_div_block" style="background-color: rgba(249,153,113, 1)">
          <div class="block_text">All-Time Averages</div>
          <div class="block_text">Of The first bonded token</div>
          <div class="block_text"><span>Total Tokens: </span><span>this.totalTokens</span></div>
          <div class="block_text"><span>myDividends: </span><span>this.myDividends</span></div>
          <div class="block_text"><span>buyPrice: </span><span>this.buyPrice </span></div>
          <div class="block_text"><span>sellPrice: </span><span>this.sellPrice</span></div>
        </div>
      </div>
      <!--boot-->
      <div style ="clear:both;"></div>
      <myFoot/>
    </div>
  </div>
</template>

<script>
import router from '@/router/index.js'
import myTitle from '../components/Title'
import myFoot from '../components/Foot'
export default {
  name: 'FreeBlock',
  components: {myTitle, myFoot},
  data () {
    return {
      buyPrice: '',
      sellPrice: '',
      totalTokens: '',
      titleLogo: {
        backgroundImage: 'url(/static/pic/logo72.jpg)',
        backgroundRepeat: 'no-repeat',
        backgroundSize: 'cover',
        width: '52px',
        height: '52px',
        margin: 'auto',
        border: '2px solid#00000'
      }
    }
  },
  created () {

  },
  methods: {
    goss () {
      router.push({name: 'Seashells', params: {referredBy: 'NoReferrer'}})
    }
  }

}
</script>

<style scoped>
  .title {
    font-family: h;
    width: 100%;
    height: 99px;
    text-align: center;
    background-color:cornflowerblue;
    color: white;
  }
  .logoTitle {
    background-image: url('/static/pic/logo72.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    width: 52px;
    height: 52px;
    margin: 0 auto;
    border: 1px solid white;
    border-radius: 3px;
  }
  .white {
    color: white;
    margin: 0px;
    padding: 0px;
  }
  .part2_div_block{
    text-align:left;
    width: 320px;
    height: 210px;
    border:1px solid white;
    float: left;
  }
  .part3_4_div_block {
    text-align:left;
    width: 420px;
    height: 210px;
    border:1px solid#ccc;
    background:rgba(250,00,25,0.9)
  }
  .block_text {
    margin-left: 5px;
    margin-top: 9px;
    margin-right: 5px;
    margin-bottom: 5px;
  }
  .baseClass{
    margin-top: 19px;
    margin-bottom: 19px;
  }
  .fontLinght_short {
    text-shadow: 0 0 10px #fff,
    0 0 20px #6495ED;
  }
  .fontLinght_long {
    text-shadow: 0 0 10px #fff,
    0 0 20px #fff,
    0 0 30px #fff,
    0 0 40px #8df;
  }
  p {
    font-family: 'Audiowide';
    text-align: center;
    color: #00a67c;
    font-size: 7em;
    -webkit-transition: all 1.5s ease;
    transition: all 1.5s ease;
  }

  p:hover {
    color: #fff;
    -webkit-animation: Glow 1.5s ease infinite alternate;
    animation: Glow 1.5s ease infinite alternate;
  }

  @-webkit-keyframes Glow {

    from {
      text-shadow: 0 0 10px #fff,
      0 0 20px #fff,
      0 0 30px #fff,
      0 0 40px #00a67c,
      0 0 70px #00a67c,
      0 0 80px #00a67c,
      0 0 100px #00a67c,
      0 0 150px #00a67c;
    }
    to {
      text-shadow: 0 0 5px #fff,
      0 0 10px #fff,
      0 0 15px #fff,
      0 0 20px #00a67c,
      0 0 35px #00a67c,
      0 0 40px #00a67c,
      0 0 50px #00a67c,
      0 0 75px #00a67c;
    }
  }

  @keyframes Glow {

    from {
      text-shadow: 0 0 10px #fff,
      0 0 20px #fff,
      0 0 30px #fff,
      0 0 40px #00a67c,
      0 0 70px #00a67c,
      0 0 80px #00a67c,
      0 0 100px #00a67c,
      0 0 150px #00a67c;
    }

    to {
      text-shadow: 0 0 5px #fff,
      0 0 10px #fff,
      0 0 15px #fff,
      0 0 20px #00a67c,
      0 0 35px #00a67c,
      0 0 40px #00a67c,
      0 0 50px #00a67c,
      0 0 75px #00a67c;
    }
  }
</style>



